.cover {
  background-size: cover;
  background-position: center;
  height: 100%;
  width: 100%;
  position: fixed;
  z-index: -1;
  -ms-filter: blur(10px);
  -webkit-filter: blur(10px);
  filter: blur(10px);
  opacity: 0.5;
}

.cover-glass {
  background-color: rgba(0, 0, 0, 0.25);
  height: 100%;
  width: 100%;
  position: fixed;
  z-index: -1;
}



